/**
 * MotorRouteContainer.js
 * The wrapper container for  motor
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import { change, reset } from 'redux-form';
import { withRouter } from 'react-router';
// import { Prompt } from 'react-router-dom';
import PropTypes from 'prop-types';
import Page from 'components/Page/Page';
import Loadable from 'react-loadable';

import { clearMotorData } from '../../actions/motorActions';
import { resetCompare } from '../../actions/compareActions';
import { getPromoGiveaways, resetInsuranceForm, getConstants, FORMSTATES } from '../../actions/insuranceActions';
import { isProduction } from '../../lib/tagFunctions';
import { routeHandler } from '../../lib/helperFunctions';

// import StepProgressBar from '../../components/Templates/StepProgressBar/StepProgressBar';
import RefreshTimer from '../../components/Templates/RefreshTimer/RefreshTimer';

// import MotorMainContainer from './MotorMainContainer';
// import MotorMoreContainer from './MotorMoreContainer';
// import MotorCompanyContainer from './MotorCompanyContainer';
// import MotorChoosePlanContainer from './MotorChoosePlanContainer';
// import MotorDetailContainer from './MotorDetailContainer';

const detailForm = 'motor';
const detailFormName = 'motor-detail';
const detailMoreFormName = 'motor-more';

const StepProgressBar = Loadable({
  loader: () => import(/* webpackChunkName: "StepProgressBar" */ '../../components/Templates/StepProgressBar/StepProgressBar'),
  loading: () => null,
  modules: ['StepProgressBar']
});

const MotorMainContainer = Loadable({
  loader: () => import(/* webpackChunkName: "MotorMainContainer" */ './MotorMainContainer'),
  loading: () => null,
  modules: ['MotorMainContainer']
});

const MotorMoreContainer = Loadable({
  loader: () => import(/* webpackChunkName: "MotorMoreContainer" */ './MotorMoreContainer'),
  loading: () => null,
  modules: ['MotorMoreContainer']
});

const MotorCompanyContainer = Loadable({
  loader: () => import(/* webpackChunkName: "MotorCompanyContainer" */ './MotorCompanyContainer'),
  loading: () => null,
  modules: ['MotorCompanyContainer']
});

const MotorChoosePlanContainer = Loadable({
  loader: () => import(/* webpackChunkName: "MotorChoosePlanContainer" */ './MotorChoosePlanContainer'),
  loading: () => null,
  modules: ['MotorChoosePlanContainer']
});

const MotorDetailContainer = Loadable({
  loader: () => import(/* webpackChunkName: "MotorDetailContainer" */ './MotorDetailContainer'),
  loading: () => null,
  modules: ['MotorDetailContainer']
});

class MotorRouteContainer extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { formIndex, isMounted } = prevState;
    const { formState, match, history } = nextProps;

    if (isMounted) {
      const routeResult = routeHandler(detailForm, formState, formIndex, match, history);
      if (routeResult && routeResult.formIndex !== null) {
        return ({
          formIndex: routeResult.formIndex,
        });
      }
    }
    return null;
  }

  constructor(props) {
    super(props);

    this.state = {
      formIndex: 0,
      isInitialLoad: true,
    };

    const {
      resetStoreForm,
      motorPromoGiveAway,
      clearData,
      history,
    } = this.props;

    // history.replace(`/motor${history.location.search}`);

    if (motorPromoGiveAway === undefined) {
      this.props.getPromoGiveaways();
    }

    clearData();
    this.props.resetCompare();
    this.props.resetInsuranceForm();

    resetStoreForm('motor');
    resetStoreForm('motor-more');
    resetStoreForm('motor-detail');

    this.renderStepProgress = this.renderStepProgress.bind(this);
    this.renderMotorForms = this.renderMotorForms.bind(this);
    this.setInitialFormState = this.setInitialFormState.bind(this);
    this.changeUserDetails = this.changeUserDetails.bind(this);
  }

  componentDidMount() {
    // window.onbeforeunload = () => true;

    /* window.addEventListener('beforeunload', (e) => {
      const confirmationMessage = 'It looks like you have been editing something. '
                              + 'If you leave before saving, your changes will be lost.';

      (e || window.event).returnValue = confirmationMessage; //Gecko + IE
      return confirmationMessage; //Gecko + Webkit, Safari, Chrome etc.
    }); */

    this.setInitialFormState();
    this.setState({
      // eslint-disable-next-line
      isMounted: true,
    });
  }

  componentDidUpdate(prevProps) {
    const { isInitialLoad } = this.state;
    const { userDetails } = this.props;

    if (isInitialLoad && _.isEqual(userDetails, prevProps.userDetails)) {
      this.changeUserDetails();
    }
  }

  componentWillUnmount() {
    const { clearData, resetStoreForm } = this.props;
    clearData();
    this.props.resetCompare();
    this.props.resetInsuranceForm();

    resetStoreForm('motor');
    resetStoreForm('motor-more');
    resetStoreForm('motor-detail');
  }

  /* shouldComponentUpdate(nextProps) {
    const {
      formState,
      constants,
      charged,
      userDetails,
    } = this.props;

    return (
      formState !== nextProps.formState ||
      constants !== nextProps.constants ||
      charged !== nextProps.charged ||
      userDetails !== nextProps.userDetails
    );
  } */

  setInitialFormState() {
    const { changeForm } = this.props;

    changeForm(detailForm, 'vehicle_make', 'AUDI');
    setTimeout(() => changeForm(detailForm, 'vehicle_model', 'A3 1.0'), 2);
    changeForm(detailForm, 'year_registered', 2018);

    changeForm(detailFormName, 'vehicle_make', 'AUDI');
    changeForm(detailFormName, 'vehicle_model', 'A3 1.0');
    changeForm(detailFormName, 'reg_year', 2018);

    changeForm(detailMoreFormName, 'year_born', 1990);
    changeForm(detailMoreFormName, 'years_driving', 'One');
    changeForm(detailMoreFormName, 'number_of_claims', 0);

    this.changeUserDetails();
  }

  changeUserDetails() {
    const { isInitialLoad } = this.state;
    const { userDetails, changeForm } = this.props;

    if (userDetails) {
      const year = userDetails.dateOfBirth && userDetails.dateOfBirth.length >= 4 ? parseInt(userDetails.dateOfBirth.slice(userDetails.dateOfBirth.length - 4), 10) : null;
      const maritalStatus = _.startCase(userDetails.marital_status);
      const gender = _.startCase(userDetails.gender);
      const surname = userDetails.surname ? userDetails.surname : '';
      const firstname = userDetails.firstName ? ` ${userDetails.firstName}` : '';

      changeForm(detailMoreFormName, 'year_born', year);
      changeForm(detailMoreFormName, 'marital_status', maritalStatus);
      changeForm(detailMoreFormName, 'gender', gender);

      changeForm(detailFormName, 'email', userDetails.email);
      changeForm(detailFormName, 'name', `${surname}${firstname}`);
      changeForm(detailFormName, 'nric', userDetails.nric);
      changeForm(detailFormName, 'birth_date', userDetails.dateOfBirth);
      changeForm(detailFormName, 'gender', gender);
      changeForm(detailFormName, 'marital_status', maritalStatus);
      changeForm(detailFormName, 'postal', userDetails.postal_code);
      changeForm(detailFormName, 'mobile_number', userDetails.contact);

      if (isInitialLoad) {
        this.setState({
          isInitialLoad: false,
        });
      }
    }
  }

  renderStepProgress() {
    const { formIndex } = this.state;
    const { charged } = this.props;

    return (
      <StepProgressBar
        maxWidth="556px"
        formState={formIndex}
        inputList={[
          'About yourself',
          'Choose Company',
          'Choose Plan',
          'Details',
        ]}
        form="motor"
        stateList={[
          FORMSTATES.MORE,
          FORMSTATES.CHOOSECOMPANY,
          FORMSTATES.CHOOSEPLAN,
          FORMSTATES.DETAILFORM,
        ]}
        stateDescriptions={[
          { main: 'Tell us more about yourself' },
          { main: ' ' /* Choose your preferred company' */},
          { main: ' ' /* 'Choose your preferred plan' */},
          { main: 'Details' },
        ]}
        charged={charged}
      />
    );
  }

  renderMotorForms() {
    const { formIndex } = this.state;
    const {
      userDetails,
    } = this.props;

    const isLoggedIn = !_.isEmpty(userDetails);

    switch (formIndex) {
      case FORMSTATES.MORE.index:
        return <MotorMoreContainer isLoggedIn={isLoggedIn} />;
      case FORMSTATES.CHOOSECOMPANY.index:
        return <MotorCompanyContainer isLoggedIn={isLoggedIn} />;
      case FORMSTATES.CHOOSEPLAN.index:
        return <MotorChoosePlanContainer isLoggedIn={isLoggedIn} />;
      case FORMSTATES.DETAILFORM.index:
        return (
          <MotorDetailContainer
            isLoggedIn={isLoggedIn}
            piggyCallback={this.changeUserDetails}
          />
        );
      default:
        return <MotorMainContainer isLoggedIn={isLoggedIn} />;
    }
  }

  render() {
    const {
      constants,
      charged,
      motorPromoGiveAway,
    } = this.props;

    if (!constants || motorPromoGiveAway === undefined) {
      return null;
    }

    const meta = isProduction ? 'car insurance, motor insurance, auto insurance, vehicle insurance, car insurance quotes, compare car insurance, cheapest car insurance, car insurance quotes online' : '';
    return (
      <Page id="motor">
        <Helmet>
          <meta name="keywords" content={meta} />
          <title>PolicyPal 2.0 - Motor Insurance</title>
          <meta name="title" content="PolicyPal 2.0 - Motor Insurance" />
          <meta name="description" content="Browse products for motor insurance." />
        </Helmet>
        {/* {
          formState !== FORMSTATES.MAIN ?
            <Prompt
              when={true}
              message="Are you sure you want to leave current page?"
            />
          :
            null
        } */}
        {
          !charged ?
            this.renderStepProgress()
          :
            null
        }
        {this.renderMotorForms()}
        <RefreshTimer ref={(node) => {
            if (!this.refresher) {
              node.invokeNewTimer();
            }
            this.refresher = node;
          }}
        />
      </Page>
    );
  }
}

MotorRouteContainer.defaultProps = {
  constants: null,
  charged: null,
  motorPromoGiveAway: undefined,
  userDetails: null,
};

MotorRouteContainer.propTypes = {
  motorPromoGiveAway: PropTypes.object,
  clearData: PropTypes.func.isRequired,
  resetCompare: PropTypes.func.isRequired,
  resetInsuranceForm: PropTypes.func.isRequired,
  resetStoreForm: PropTypes.func.isRequired,
  getPromoGiveaways: PropTypes.func.isRequired,
  changeForm: PropTypes.func.isRequired,
  constants: PropTypes.object,
  userDetails: PropTypes.object,
  charged: PropTypes.bool,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  formState: state.insurance.formState,
  constants: state.insurance.constants,
  charged: state.insurance.charged,
  userDetails: state.userDetails.data,
  motorPromoGiveAway: state.insurance.promoGiveaways.motor,
});

const mapDispatchToProps = dispatch => ({
  resetStoreForm: form => (
    dispatch(reset(form))
  ),
  resetInsuranceForm: () => (
    dispatch(resetInsuranceForm())
  ),
  getConstantsData: () => {
    dispatch(getConstants());
  },
  changeForm: (form, key, value) => (
    dispatch(change(form, key, value))
  ),
  getPromoGiveaways: () => {
    dispatch(getPromoGiveaways('motor'));
  },
  resetCompare: () => {
    dispatch(resetCompare());
  },
  clearData: () => {
    dispatch(clearMotorData());
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MotorRouteContainer));
